html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', 'Tahoma', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  background-color: #f5f5f5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}



